
.hero {
    position: relative;
    width: 100%;
    min-height: 100vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    }
      
    .hero-image {
    position: absolute;
    inset: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0;
    transition: opacity 2s ease-in-out;
    }
    
    .hero-image.visible {
    opacity: 1;
    }
    
    .hero-text {
    position: relative;
    z-index: 2;
    color: white;
    text-align: center;
    max-width: 800px;
    }
    .hero-text h1{
    font-size: 70px;
    font-weight: 300;
    }
    
    .hero-text p{
    font-family: "DM Serif Text", sans-serif;
    font-weight: 400;
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4;
    font-size: 30px;
    color: white;
    }
      
    
    
    
    
    
