footer {
    background-color: rgba(0, 0, 0, 0.89);
    color: white;
    padding: 10px;
    text-align: center;
    position: relative;
    bottom: 0;
    width: 100%;
    margin-top: 80px;
    }
    
    .footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    }
    
    .footer-content .p-footer {
    margin: 5px 0;
    font-size: 12px;
    color: #ffa947d5;
    font-family: "DM Serif Text", sans-serif;
    font-weight: 300;
    }
    
    .social-links {
    list-style: none;
    padding: 0;
    display: flex;
    gap: 16px;
    }
    .social-links p{
    color: #ffa947d5;
     
    }
    .social-links li {
    display: inline-block;
    }
    
    .social-links a {
    text-decoration: none;
    color: #ffa947d5;
    font-size: 16px;
    transition: color 0.3s ease;
    }
    
    .social-links a:hover {
    color: white;
    }